import { Component, OnInit, AfterViewInit } from '@angular/core';

declare var $:any;

declare interface Table_With_Checkboxes {
    id?: number;
    name: string;
    phone: string;
    email: string;
    active?: boolean;
}
declare interface TableData {
    headerRow: string[];
    dataRows: string[][];
}
declare interface TableData2 {
    headerRow: string[];
    dataRows: Table_With_Checkboxes[];
}

@Component({
    moduleId: module.id,
    selector: 'extended-table-cmp',
    templateUrl: 'transaction.component.html'
})

export class TransactionComponent implements OnInit{
    public tableData1: TableData;
    state_plain: boolean = true;

    ngOnInit(){

        this.tableData1 = {
            headerRow: [ '#', 'Transaction id','Description', 'Date', 'Amount', 'Actions'],
            dataRows: [
                ['1', '221334', 'monthly subscription', 'March 2018', '9000'],
                ['2', '23478', 'Annual Sub', 'Feb 2018','60000'],
                ['3', '2347898', 'Monthly sub', 'example@bvs.com','4555'],
                ['4','564534', 'Monthly sub', 'example@bvs.com','90000'],
                ['5', '211311', 'Annual Sub', 'example@bvs.com','9000']
            ]
        };

    }

    ngAfterViewInit(){
        // Init Tooltips
        $('[rel="tooltip"]').tooltip();

        $('.switch-plain').bootstrapSwitch({
            onColor:'',
            onText: '',
            offText: ''
        });
    }
}



